
var jquery = require("jquery");
window.$ = window.jQuery = jquery; // notice the definition of global variables here
require('/js/jquery-3.4.1.min.js');
require('/js/jquery.smartmenus.min.js');


$(function() {
  $('#main-menu').smartmenus({
    keepHighlighted: true,
    subMenusMinWidth: "200px",
    subIndicatorsText: '<i class="fas fa-caret-down"></i>'
  })
});
$(function() {
  var $mainMenuState = $('#main-menu-state');
  if ($mainMenuState.length) {
    // animate mobile menu
    $mainMenuState.change(function(e) {
      var $menu = $('#main-menu');
      if (this.checked) {
        $menu.hide().slideDown(250, function() { $menu.css('display', ''); });
      } else {
        $menu.show().slideUp(250, function() { $menu.css('display', ''); });
      }
    });
    // hide mobile menu beforeunload
    $(window).on('beforeunload unload', function() {
      if ($mainMenuState[0].checked) {
        $mainMenuState[0].click();
      }
    });
  }
});

var product1 = document.getElementById('product-1');
doModal(product1);
function doModal(item){
  var slideIndex = 1;
  showSlides(slideIndex);
  function showSlides(e) {
    var i;
    var slides = item.getElementsByClassName("mySlides");
    if (e > slides.length) {slideIndex = 1}
    if (e < 1) {slideIndex = slides.length}
    for (i = 0; i < slides.length; i++) {
      slides[i].style.display = "none";
    }
    slides[slideIndex-1].style.display = "flex";
  }
  function plusSlides() {
    showSlides(slideIndex += 1);
  }
  function minusSlides() {
    showSlides(slideIndex = slideIndex - 1);
  }
  // Open the Modal
  item.getElementsByClassName('product-item__block-image')[0].addEventListener('click', openModal);
  item.getElementsByClassName('close')[0].addEventListener('click', closeModal);
  function openModal() {
    // Next/previous controls
    var modal = item.getElementsByClassName("product-body__modal")[0];
    modal.style.display = "flex";
    var prev = modal.getElementsByClassName('prev');
    prev[0].addEventListener('click', minusSlides);
    var next = modal.getElementsByClassName('next');
    next[0].addEventListener('click', plusSlides);
  }
  // Close the Modal
  function closeModal() {
    item.getElementsByClassName("product-body__modal")[0].style.display = "none";
  }
  // Thumbnail image controls
  function currentSlide(n) {
    showSlides(slideIndex = n);
  }
}
